import {
  ChangeDetectionStrategy,
  Component,
  Input,
  ViewEncapsulation,
  Output,
  EventEmitter,
  ContentChild,
  ViewChild,

  OnChanges
} from '@angular/core';
import { InputBoolean } from 'ng-zorro-antd';
import { CdkDrag } from '@angular/cdk/drag-drop';
import { CmacsDividerComponent } from '../cmacs-divider/cmacs-divider.component';

export type FloatingMenuType = 'top' | 'bottom' | 'left' | 'right';

export const FLOATING_MENU_LOCALIZATION = {
  'Dock To Left': 'Dock To Left',
  'Dock To Right': 'Dock To Right',
  'Dock To Top': 'Dock To Top',
  'Dock To Bottom': 'Dock To Bottom',
  'Minimize Toolbar': 'Minimize Toolbar'
};

@Component({
  selector: 'cmacs-floating-menu',
  exportAs: 'cmacsFloatingMenu',
  templateUrl: './cmacs-floating-menu.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./cmacs-floating-menu.component.css'],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.carrot-bottom-menu]': `carrot === 'bottom'`,
    '[class.carrot-top-menu]': `carrot === 'top'`,
    '[class.cmacs-floating-menu-align-x]': `position === 'bottom' || position === 'top'`,
    '[class.cmacs-floating-menu-align-y]': `position === 'left' || position === 'right'`,
    '[style.top]': `top`,
    '[style.bottom]': `bottom`,
    '[style.left]': `left`,
    '[style.right]': `right`,
  }
})

export class CmacsFloatingMenuComponent implements OnChanges{

  _minimizeToolbar = false;
  _i18n = FLOATING_MENU_LOCALIZATION;

  @ViewChild(CdkDrag) cdkDrag: CdkDrag;

  @Input() position: FloatingMenuType = 'bottom';
  @Input() @InputBoolean() visible = false;
  @Input() @InputBoolean() showExtras = true;
  @Input() dragBoundary: string;
  @Output() positionChange: EventEmitter<FloatingMenuType> = new EventEmitter<FloatingMenuType>();

  @Input() carrot: string = '';
  @Input() top: string;
  @Input() bottom: string;
  @Input() left: string;
  @Input() right: string;

  @Input() topBoundary: string = '0';
  @Input() bottomBoundary: string = '0';
  @Input() leftBoundary: string = '0';
  @Input() rightBoundary: string = '0';

  @Input() i18n: any = FLOATING_MENU_LOCALIZATION;
  
  constructor() { }

  ngOnChanges() {
    if (this.cdkDrag) {
      this.resetDragDrop();
    }
  }

  minimizeToolbar() {
    this.resetDragDrop();
    this._minimizeToolbar = true;
  }

  expandToolbar() {
    this.resetDragDrop();
    this._minimizeToolbar = false;
  }

  dockToLeft() {
    this.resetDragDrop();
    this.top = null;
    this.left = this.leftBoundary;
    this.right = null;
    this.bottom = null;
    this.position = 'left';
    this.positionChange.emit(this.position);
  }

  dockToRight() {
    this.resetDragDrop();
    this.top = null;
    this.left = null;
    this.right = this.rightBoundary;
    this.bottom = null;
    this.position = 'right';
    this.positionChange.emit(this.position);
  }

  dockToTop() {
    this.resetDragDrop();
    this.top = this.topBoundary;
    this.left = null;
    this.right = null;
    this.bottom = null;
    this.position = 'top';
    this.positionChange.emit(this.position);
  }

  dockToBottom() {
    this.resetDragDrop();
    this.bottom = this.bottomBoundary;
    this.left = null;
    this.right = null;
    this.top = null;
    this.position = 'bottom';
    this.positionChange.emit(this.position);
  }

  getPlacement() {
    switch (this.position) {
      case 'bottom':
        return 'top';
      case 'top':
        return 'bottom';
      case 'left':
        return 'right';
      case 'right':
        return 'left';
    }
  }

  resetDragDrop() {
    this.cdkDrag._dragRef['_previewRect'] = undefined;
    this.cdkDrag._dragRef['_boundaryRect'] = undefined;
    this.cdkDrag.reset();
  }
}
