import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { InputBoolean, NzUpdateHostClassService } from 'ng-zorro-antd/core';

@Component({
  selector: 'cmacs-divider',
  exportAs: 'cmacsDivider',
  templateUrl: './cmacs-divider.component.html',
  styleUrls: ['./cmacs-divider.component.css'],
  preserveWhitespaces: false,
  providers: [NzUpdateHostClassService],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CmacsDividerComponent implements OnChanges, OnInit {
  @Input() text: string | TemplateRef<void>;
  @Input() type: 'horizontal' | 'vertical' = 'vertical';
  @Input() orientation: 'left' | 'right' | '' = '';
  @Input() @InputBoolean() dashed = false;

  private setClass(): void {
    const orientationPrefix = this.orientation.length > 0 ? '-' + this.orientation : this.orientation;
    this.nzUpdateHostClassService.updateHostClass(this.elementRef.nativeElement, {
      ['ant-divider']: true,
      [`ant-divider-${this.type}`]: true,
      [`ant-divider-with-text${orientationPrefix}`]: this.text,
      [`ant-divider-dashed`]: this.dashed
    });
  }

  constructor(public elementRef: ElementRef, private nzUpdateHostClassService: NzUpdateHostClassService) { }

  ngOnChanges(): void {
    this.setClass();
  }

  ngOnInit(): void {
    this.setClass();
  }
}
