import {
  Component,
  EventEmitter,
  Input,
  Output,
  OnInit,
  ViewEncapsulation
} from '@angular/core';

@Component({
  selector: 'cmacs-editor',
  exportAs: 'cmacsEditor',
  templateUrl: './cmacs-editor.component.html',
  styleUrls: ['./cmacs-editor.component.css'],

  encapsulation: ViewEncapsulation.None
})
export class CmacsEditorComponent implements OnInit {

  showEditor = false;
  @Output() oninit: EventEmitter<any> = new EventEmitter<any>();
  @Output() onchange: EventEmitter<any> = new EventEmitter<any>();
  @Output() onblur: EventEmitter<any> = new EventEmitter<any>();
  @Output() onkeyup: EventEmitter<any> = new EventEmitter<any>();
  @Input() disabled!: boolean;
  @Input() height = '250px';
  @Input() statusbar = false;
  @Input() resize = false;
  // tslint:disable-next-line: max-line-length
  @Input() toolbarmobile = ['bold', 'italic', 'underline', 'strikethrough', 'alignleft', 'aligncenter', 'alignright', 'alignjustify', 'bullist', 'numlist', 'forecolor'];
  @Input() toolbar = 'bold italic underline strikethrough  | alignleft aligncenter alignright alignjustify | bullist numlist | forecolor';
  @Input() tinyMceSettings!: any;

  constructor() { }

  ngOnInit() {

    if (this.tinyMceSettings === undefined) {
      this.tinyMceSettings = {
        mobile: {
          theme: 'mobile',
          plugins: ['image table textcolor'],
          toolbar: this.toolbarmobile
        },
        menubar: false,
        image_title: true,
        resize: this.resize,
        automatic_uploads: true,
        height: this.height,
        statusbar: this.statusbar,
        file_picker_types: 'image',
        images_upload_url: '#',
        setup: (editor: any) => {
          editor.on('init', (obj) => {
            this.oninit.emit(obj);
          });
          editor.on('blur', (obj) => {
            this.onblur.emit(obj);
          });
          editor.on('keyup', (obj) => {
            this.onkeyup.emit(obj);
          });
          editor.on('Change', (obj) => {
            this.onchange.emit(obj);
          });
        },
        plugins: ['image table textcolor'],
        toolbar: this.toolbar
      };
    }

    setTimeout(() => {
      this.showEditor = true;
    }, 100);
  }

}
